import React, { Component } from 'react';
import { redirectAcessURL } from '../service/getAcessToken';
import './Login.css';
import logoSpotify from '../images/spotify-logo.png';
import CardIllustration from '../components/CardIllustration';
import illustration01 from '../images/illustration-01.svg';
import illustration02 from '../images/illustration-02.svg';

class Login extends Component {
  handleLogin = () => {
    const url = redirectAcessURL();
    window.location = url;
  }

  render() {
    return (
    <>
        <main>
            <section className="mainLogin">
                <p>Descubra as msicas e artistas que voc mais escutou ao longo do tempo no Spotify</p>
                <button onClick={ this.handleLogin }>
                  ENTRAR
                  <img src={ logoSpotify } alt="Logo do Spotify" />
                </button>
            </section>
            <section>
              <CardIllustration
                title="Dados a partir da sua conta do Spotify"
                description="Veja os artistas, msicas e gneros que voc mais escuta, mude entre 1 ms, 6 meses ou AllTime para ver os dados por perodo. Esses dados so atualizados diariamente."
                url={ illustration02 }
              />
              <CardIllustration
                title="Crie Playlists com as msicas e artistas que voc mais escuta"
                description="As playlists contam com at 50 msicas e seleo de perodo, crie ela aqui e escute direto no App"
                url={ illustration01 }
              />
            </section>
        </main>
    </>
    )
  }
}

export default Login